<!--  -->
<template>
  <div>
    <!-- <h4 style="margin-left: 10px;color: #d6a20f;"</h4> -->
    <a-card>
      <a-row :gutter="20">
        <!--  -->
        <a-col :span="6">
          <a-input-search v-model="queryParam.keywords" placeholder="" enter-button allowClear @search="getArtList" />
        </a-col>
        <!--  -->
        <a-col :span="4">
          <a-button type="primary" @click="$router.push('/addart')"></a-button>
        </a-col>
        <a-col :span="6">
          <a-select defaultValue="" style="width: 200px;" @change="cateChange">
            <a-select-option v-for="cate in cateList" :key="cate.id" :value="cate.id">
              {{cate.name}}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>

      <!--  -->
      <a-table :columns="columns" rowKey="ID" :pagination="pagination" :data-source="Artlist" @change="handleTableChange" bordered>

        <!--  -->
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" style="margin: 0 5px;" @click="$router.push(`/addart/${data.ID}`)">
            </a-button>
            <a-button type="danger" icon="delete" style="margin: 0 5px;" @click="deleteArt(data.ID)">
            </a-button>

          </div>
        </template>
      </a-table>

    </a-card>
  </div>
</template>

<script>
// 
const columns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    width: '5%',
    key: 'id',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'Category.name',
    width: '10%',
    key: 'name',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'title',
    width: '30%',
    key: 'title',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'desc',
    width: '30%',
    key: 'desc',
    align: 'center',
  },
  {
    title: '',
    width: '30%',
    key: 'action',
    scopedSlots: { customRender: 'action' },
    align: 'center',
  },
]
export default {
  data () {
    return {
      // 
      pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      // 
      Artlist: [],
      // 
      columns,
      // 
      queryParam: {
        keywords: '',  // 
        pagesize: 5,
        pagenum: 1,
      },
      // 
      cateList: [],

    }
  },
  // - this
  created () {
    this.getArtList();
    this.getCateList();
  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    // 
    async getArtList () {
      const { data: res } = await this.$axios.get('articles', {
        params: {
          keywords: this.queryParam.keywords,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      });
      // console.log(res);
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.Artlist = res.data;
      this.pagination.total = res.total;
    },

    // 
    async getCateList () {
      const { data: res } = await this.$axios.get('categories');
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.cateList = res.data;
    },

    // 
    // eslint-disable-next-line no-unused-vars
    handleTableChange (pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;
      this.queryParam.pagesize = pagination.pageSize;
      this.queryParam.pagenum = pagination.current;

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1;
        pager.current = 1;
      }
      this.pagination = pager;
      this.getArtList();
    },

    // 
    deleteArt (id) {
      this.$confirm({
        title: '',
        content: '?',
        onOk: async () => {
          const res = await this.$axios.delete(`article/${id}`);
          console.log(res);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$message.success('');
          this.getArtList();
        },
        onCancel: () => {
          this.$message.info('');
        },
      });
    },

    //
    cateChange (val) {
      this.getCateArt(val);
    },
    //
    async getCateArt (id) {
      const { data: res } = await this.$axios.get(`category/${id}/artlist`);
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.Artlist = res.data;
      this.pagination.total = res.total;
    }

  }
}
</script>
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>