<!--  -->
<template>
  <div>
    <a-card>
      <h4 style="margin-left: 10px;color: #d6a20f;text-align: center;">{{id? '':''}}</h4>
      <a-form-model :model="artInfo" ref="artInfoRef" :rules="artInfoRules" :hideRequiredMark="true">
        <!--  -->
        <a-form-model-item label="" prop="title">
          <a-input v-model="artInfo.title"></a-input>
        </a-form-model-item>
        <!--  -->
        <a-form-model-item label="" prop="cid">
          <a-select v-model="artInfo.cid" @change="cateChange" placeholder="">
            <a-select-option v-for="cate in cateList" :key="cate.id" :value="cate.id">
              {{cate.name}}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <!--  -->
        <a-form-model-item label="" prop="flag">
          <a-input v-model="artInfo.flag"></a-input>
        </a-form-model-item>
        <!--  -->
        <a-form-model-item label="" prop="desc">
          <a-textarea v-model="artInfo.desc"></a-textarea>
        </a-form-model-item>
        <!--  -->
        <a-form-model-item label="" prop="content">
          <div style="height: 600px;">
            <!-- Markdown -->
            <mavon-editor v-model="artInfo.content" :toolbars="toolbars" :codeStyle="codeStyle" ref=artContentMd @imgAdd="handleImgAdd" @imgDel="handleImgDel" placeholder="..." style="height:100%;z-index: 999;font-size: 16px;" />
          </div>

        </a-form-model-item>
        <!--  -->
        <a-form-model-item>
          <a-button type="primary" style="margin: 0 10px 0 0 ;" @click="artSubmit(artInfo.id)">
            
          </a-button>
          <a-button type="danger" style="margin: 0 10px;" @click="addCancel()"></a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  props: ['id'],
  data () {
    return {
      artInfo: {
        id: 0,
        title: '',
        cid: undefined,
        flag: '',
        desc: '',
        content: '',
      },
      cateList: [],

      // 
      artInfoRules: {
        title: [
          { required: true, message: "", trigger: "blur" },
        ],
        cid: [
          { required: true, message: "", trigger: "change" },
        ],
        flag: [
          { required: true, message: "", trigger: "blur" },
        ],
        desc: [
          { required: true, message: "", trigger: "blur" },
          { max: 120, message: "20", trigger: "change" },
        ],
        content: [
          { required: true, message: "", trigger: "blur" },
        ],
      },

      // Markdown
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        mark: true, // 
        superscript: true, // 
        quote: true, // 
        ol: true, // 
        link: true, // 
        imagelink: true, // 
        help: true, // 
        code: true, // code
        subfield: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        undo: true, // 
        trash: true, // 
        save: true, // eventssave
        navigation: true, // 
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 

        preview: false, // 
      },
      // markdown monokai-sublime
      codeStyle: "atom-one-light",//

    }
  },
  // - this
  created () {
    this.getCateList();
    if (this.id) {
      this.getArtInfo(this.id);
    }
  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    //
    async getArtInfo (id) {
      const { data: res } = await this.$axios.get(`/article/${id}`);
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.artInfo = res.data;
      this.artInfo.id = res.data.ID;
    },

    // 
    async getCateList () {
      const { data: res } = await this.$axios.get('categories');
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.cateList = res.data;
    },

    //
    cateChange (val) {
      this.artInfo.cid = val;
    },

    //()
    artSubmit (id) {
      // eslint-disable-next-line no-unused-vars
      this.$refs.artInfoRef.validate(async (valid) => {
        if (id == 0) {  //
          const { data: res } = await this.$axios.post("/article/add", this.artInfo);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$router.push("/artlist");
          this.$message.success("");
        } else {  //
          const { data: res } = await this.$axios.put(`/article/${id}`, this.artInfo);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$router.push("/artlist");
          this.$message.success("");
        }
      });
    },

    // 
    addCancel () {
      this.$refs.artInfoRef.resetFields();
    },

    // Markdown
    // change(value, render) {
    //     this.html = render;
    //     this.artInfo.content = value;
    //     this.artInfo.content = render;
    // },

    // Markdown
    handleImgAdd (pos, $file) {
      // .
      var formdata = new FormData();
      formdata.append('file', $file);
      this.$axios.post("/upload", formdata, {
        headers: {
          'Content-Type': 'multipart/form-data',
        }
      }).then(res => {
        // console.log("res:" , res);
        var url = res.data.url;
        // .url![...](0) -> ![...](url)
        this.$refs.artContentMd.$img2Url(pos, url);
      });
    },

    // Markdown
    handleImgDel () {
      console.log("");
    },
  }
}
</script>
