<!--  -->
<template>
  <div>
    <!-- <h4 style="margin-left: 10px;color: #d6a20f;"></h4> -->
    <a-card>
      <a-row :gutter="20">
        <!--  -->
        <a-col :span="6">
          <a-input-search v-model="queryParam.username" placeholder="" enter-button allowClear @search="getUserList" />
        </a-col>
        <!--  -->
        <a-col :span="4">
          <a-button type="primary" @click="addUser()"></a-button>
        </a-col>
      </a-row>

      <!--  -->
      <a-table :columns="columns" rowKey="ID" :pagination="pagination" :data-source="userlist" @change="handleTableChange" bordered>
        <!--  -->
        <span slot="role" slot-scope="data">{{data == 1 ? '':''}}</span>
        <!--  -->
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" style="margin: 0 5px;" @click="editUser(data.ID)">
            </a-button>
            <a-button type="danger" icon="delete" style="margin: 0 5px;" @click="deleteUser(data.ID)">
            </a-button>
            <a-button type="info" icon="undo" style="margin: 0 5px;background-color: #f3f052;border-color: #f3f052;" @click="resetPwd(data.ID)">
            </a-button>
          </div>
        </template>
      </a-table>

      <!--  -->
      <a-modal closable title="" :visible="addUserVisible" @ok="addUserOk" @cancel="addUserCancel">
        <a-form-model :model="newUserInfo" :rules="addUserRules" ref="addUserRef">
          <a-form-model-item label="" prop="username">
            <a-input v-model="newUserInfo.username"></a-input>
          </a-form-model-item>
          <a-form-model-item has-feedback label="" prop="password">
            <a-input-password v-model="newUserInfo.password"></a-input-password>
          </a-form-model-item>
          <a-form-model-item has-feedback label="" prop="checkpass">
            <a-input-password v-model="newUserInfo.checkpass"></a-input-password>
          </a-form-model-item>
          <!--  -->
          <!-- <a-form-model-item label="" prop="role">
                        <a-select default-value="2" style="width: 120px" @change="adminChange">
                            <a-select-option key="1" value="1">
                                
                            </a-select-option>
                            <a-select-option key="2" value="2">
                                
                            </a-select-option>
                        </a-select>
                    </a-form-model-item> -->
        </a-form-model>
      </a-modal>

      <!--  -->
      <a-modal closable title="" :visible="editUserVisible" @ok="editUserOk" @cancel="editUserCancel">
        <a-form-model :model="userInfo" :rules="userRules" ref="editUserRef">
          <a-form-model-item label="" prop="username">
            <a-input v-model="userInfo.username"></a-input>
          </a-form-model-item>

          <a-form-model-item label="">
            <a-switch :checked="IsAdmin" checked-children="" un-checked-children="" @change="adminChange" />
          </a-form-model-item>
        </a-form-model>
      </a-modal>
    </a-card>
  </div>
</template>

<script>
// 
const columns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    width: '10%',
    key: 'id',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'username',
    width: '20%',
    key: 'username',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'role',
    width: '20%',
    key: 'role',
    scopedSlots: { customRender: 'role' },
    align: 'center',
  },
  {
    title: '',
    width: '30%',
    key: 'action',
    scopedSlots: { customRender: 'action' },
    align: 'center',
  },
]
export default {
  data () {
    return {
      // 
      pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      // 
      userlist: [],
      // 
      columns,
      // 
      queryParam: {
        username: '',  // 
        pagesize: 5,
        pagenum: 1,
      },
      visible: false,

      addUserVisible: false,  //
      editUserVisible: false, //
      //()
      newUserInfo: {
        id: 0,
        username: '',
        password: '',
        checkpass: '',
        role: 2,
      },
      //
      userInfo: {
        id: 0,
        username: '',
        password: '',
        checkpass: '',
        role: 2,
      },

      // 
      addUserRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 12, message: '412', trigger: 'blur' },
          { pattern: /^[A-Za-z]+[A-Za-z0-9]+$/, message: '' }
        ],
        // 
        password: [{
          validator: (rule, value, callback) => {
            if (this.newUserInfo.password == '') {
              callback(new Error(''));
            }
            if ([...this.newUserInfo.password].length < 6 || [...this.newUserInfo.password].length > 20) {
              callback(new Error('620'));
            } else {
              callback();
            }
          }, trigger: 'blur'
        }],
        // 
        checkpass: [{
          validator: (rule, value, callback) => {
            if (this.newUserInfo.checkpass == '') {
              callback(new Error(''));
            }
            if (this.newUserInfo.checkpass != this.newUserInfo.password) {
              callback(new Error(''));
            } else {
              callback();
            }
          }, trigger: 'blur'
        }],
      },

      // 
      userRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 12, message: '412', trigger: 'blur' },
          { pattern: /^[A-Za-z]+[A-Za-z0-9]+$/, message: '' }
        ],
        // 
        password: [{
          validator: (rule, value, callback) => {
            if (this.userInfo.password == '') {
              callback(new Error(''));
            }
            if ([...this.userInfo.password].length < 6 || [...this.userInfo.password].length > 20) {
              callback(new Error('620'));
            } else {
              callback();
            }
          }, trigger: 'blur'
        }],
        // 
        checkpass: [{
          validator: (rule, value, callback) => {
            if (this.userInfo.checkpass == '') {
              callback(new Error(''));
            }
            if (this.userInfo.checkpass != this.userInfo.password) {
              callback(new Error(''));
            } else {
              callback();
            }
          }, trigger: 'blur'
        }],
      },

    }
  },
  // - this
  computed: {
    IsAdmin: function () {
      if (this.userInfo.role === 1) {
        return true
      } else {
        return false
      }
    },
  },
  created () {
    this.getUserList();
  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    // 
    async getUserList () {
      const { data: res } = await this.$axios.get('users', {
        params: {
          username: this.queryParam.username,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      });
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.userlist = res.data;
      this.pagination.total = res.total;
    },

    // 
    // eslint-disable-next-line no-unused-vars
    handleTableChange (pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;
      this.queryParam.pagesize = pagination.pageSize;
      this.queryParam.pagenum = pagination.current;

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1;
        pager.current = 1;
      }
      this.pagination = pager;
      this.getUserList();
    },

    // 
    deleteUser (id) {
      this.$confirm({
        title: '',
        content: '?',
        onOk: async () => {
          const res = await this.$axios.delete(`user/${id}`);
          console.log(res);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$message.success('');
          this.getUserList();
        },
        onCancel: () => {
          this.$message.info('');
        },
      });

    },

    // 
    addUser () {
      this.addUserVisible = true;
    },

    // 
    addUserOk () {
      this.$refs.addUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$axios.post("user/add", {
          username: this.newUserInfo.username,
          password: this.newUserInfo.password,
          role: this.newUserInfo.role
        });
        if (res.status != 200) {
          return this.$message.error(res.message);
        }
        this.$refs.addUserRef.resetFields();
        this.addUserVisible = false;
        this.$message.success('');
        this.getUserList();
      });
    },
    // 
    addUserCancel () {
      this.$refs.addUserRef.resetFields();
      this.addUserVisible = false;
      this.$message.info('');
    },

    // 
    adminChange (checked) {
      // this.userInfo.role = value;
      if (checked) {
        this.userInfo.role = 1;
      } else {
        this.userInfo.role = 2;
      }
    },


    // 
    async editUser (id) {
      this.editUserVisible = true;
      const { data: res } = await this.$axios.get(`user/${id}`);
      this.userInfo = res.data;
      this.userInfo.id = id;
    },
    // 
    editUserOk () {
      this.$refs.editUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");

        const { data: res } = await this.$axios.put(`user/${this.userInfo.id}`, {
          username: this.userInfo.username,
          // password: this.userInfo.password,
          role: this.userInfo.role
        });
        if (res.status != 200) {
          return this.$message.error(res.message);
        }
        this.$refs.editUserRef.resetFields();
        this.editUserVisible = false;
        this.$message.success('');
        this.getUserList();
      });
    },
    // 
    editUserCancel () {
      this.$refs.editUserRef.resetFields();
      this.editUserVisible = false;
      this.$message.info('');
    },

    // 
    async resetPwd (id) {
      this.$confirm({
        title: '',
        content: '?',
        onOk: async () => {
          const { data: res } = await this.$axios.put(`user/${id}/reset`);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$message.success('');
        },
        onCancel: () => {
          this.$message.info('');
        },
      });
    },

  }
}
</script>
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>