<!--  -->
<template>
  <div>
    <!-- <h4 style="margin-left: 10px;color: #d6a20f;"></h4> -->
    <a-card>
      <a-row :gutter="20">
        <!--  -->
        <!-- <a-col :span="6">
                    <a-input-search v-model="queryParam.name" placeholder="" enter-button allowClear
                        @search="getCateList" />
                </a-col> -->

        <!--  -->
        <a-col :span="4">
          <a-button type="primary" @click="addCate()"></a-button>
        </a-col>
      </a-row>

      <!--  -->
      <a-table :columns="columns" rowKey="id" :pagination="pagination" :data-source="cateList" @change="handleTableChange" bordered>

        <!--  -->
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="primary" icon="edit" style="margin: 0 5px;" @click="editCate(data.id)">
            </a-button>
            <a-button type="danger" icon="delete" style="margin: 0 5px;" @click="deleteCate(data.id)">
            </a-button>

          </div>
        </template>
      </a-table>

      <!--  -->
      <a-modal closable title="" :visible="addCateVisible" @ok="addCateOk" @cancel="addCateCancel">
        <a-form-model :model="newCateInfo" :rules="addCateRules" ref="addCateRef">
          <a-form-model-item label="" prop="name">
            <a-input v-model="newCateInfo.name"></a-input>
          </a-form-model-item>

        </a-form-model>
      </a-modal>

      <!--  -->
      <a-modal closable title="" :visible="editCateVisible" @ok="editCateOk" @cancel="editCateCancel">
        <a-form-model :model="cateInfo" :rules="cateRules" ref="editCateRef">
          <a-form-model-item label="" prop="name">
            <a-input v-model="cateInfo.name"></a-input>
          </a-form-model-item>

        </a-form-model>
      </a-modal>
    </a-card>
  </div>
</template>

<script>
// 
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    width: '10%',
    key: 'id',
    align: 'center',
  },
  {
    title: '',
    dataIndex: 'name',
    width: '20%',
    key: 'name',
    align: 'center',
  },
  {
    title: '',
    width: '30%',
    key: 'action',
    scopedSlots: { customRender: 'action' },
    align: 'center',
  },
]

export default {
  data () {
    return {
      // 
      pagination: {
        pageSizeOptions: ['5', '10', '20'],
        pageSize: 5,
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => `${total}`,
      },
      // 
      cateList: [],
      // 
      columns,
      // 
      queryParam: {
        name: '',  // 
        pagesize: 5,
        pagenum: 1,
      },
      visible: false,

      addCateVisible: false,  //
      editCateVisible: false, //
      //()
      newCateInfo: {
        id: 0,
        name: '',
      },
      //
      cateInfo: {
        id: 0,
        name: '',
      },

      // 
      addCateRules: {
        // 
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: '250', trigger: 'blur' },
        ],
      },

      // 
      cateRules: {
        // 
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: '250', trigger: 'blur' },
        ],
      },
    }
  },
  // - this
  created () {
    this.getCateList();
  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$axios.get('categories', {
        params: {
          name: this.queryParam.name,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
        },
      });
      if (res.status != 200) {
        return this.$message.error(res.message);
      }
      this.cateList = res.data;
      this.pagination.total = res.total;
    },

    // 
    // eslint-disable-next-line no-unused-vars
    handleTableChange (pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current;
      pager.pageSize = pagination.pageSize;
      this.queryParam.pagesize = pagination.pageSize;
      this.queryParam.pagenum = pagination.current;

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1;
        pager.current = 1;
      }
      this.pagination = pager;
      this.getCateList();
    },

    // 
    deleteCate (id) {
      this.$confirm({
        title: '',
        content: '?',
        onOk: async () => {
          const res = await this.$axios.delete(`category/${id}`);
          console.log(res);
          if (res.status != 200) {
            return this.$message.error(res.message);
          }
          this.$message.success('');
          this.getCateList();
        },
        onCancel: () => {
          this.$message.info('');
        },
      });

    },

    // 
    addCate () {
      this.addCateVisible = true;
    },

    // 
    addCateOk () {
      this.$refs.addCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        const { data: res } = await this.$axios.post("category/add", {
          name: this.newCateInfo.name,
        });
        if (res.status != 200) {
          return this.$message.error(res.message);
        }
        this.$refs.addCateRef.resetFields();
        this.addCateVisible = false;
        this.$message.success('');
        this.getCateList();
      });
    },
    // 
    addCateCancel () {
      this.$refs.addCateRef.resetFields();
      this.addCateVisible = false;
      this.$message.info('');
    },

    // 
    async editCate (id) {
      console.log(id);
      this.editCateVisible = true;
      const { data: res } = await this.$axios.get(`category/${id}`);
      this.cateInfo = res.data;
      this.cateInfo.id = id;
    },
    // 
    editCateOk () {
      this.$refs.editCateRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");

        const { data: res } = await this.$axios.put(`category/${this.cateInfo.id}`, {
          name: this.cateInfo.name,
        });
        if (res.status != 200) {
          return this.$message.error(res.message);
        }
        this.$refs.editCateRef.resetFields();
        this.editCateVisible = false;
        this.$message.success('');
        this.getCateList();
      });
    },
    // 
    editCateCancel () {
      this.$refs.editCateRef.resetFields();
      this.editCateVisible = false;
      this.$message.info('');
    },
  }
}
</script>
<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>