<template>
	<div>
		<a-card>
			<h3></h3>
			<a-form-model>
				<!--  -->
				<a-form-model-item label="" prop="name">
					<a-input v-model="profile.name"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="motto">
					<a-input v-model="profile.motto"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="desc">
					<a-textarea v-model="profile.desc"></a-textarea>
				</a-form-model-item>
				<!-- QQ -->
				<a-form-model-item label="QQ" prop="qq_chat">
					<a-input v-model="profile.qq_chat"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="wechat">
					<a-input v-model="profile.wechat"></a-input>
				</a-form-model-item>
				<!-- bilibili -->
				<a-form-model-item label="" prop="bili">
					<a-input v-model="profile.bili"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="gitee">
					<a-input v-model="profile.gitee"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="email">
					<a-input v-model="profile.email"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="img">
					<a-input v-model="profile.img"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item label="" prop="avatar">
					<a-input v-model="profile.avatar"></a-input>
				</a-form-model-item>
				<!--  -->
				<a-form-model-item>
					<a-button type="danger" style="margin-right:15px" @click="updateProfile"></a-button>
				</a-form-model-item>
			</a-form-model>
		</a-card>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				profile: {
					id: 1,
					name: '',
					motto: '',
					desc: '',
					qq_chat: '',
					wechat: '',
					bili: '',
					gitee: '',
					email: '',
					img: '',
					avatar: ''
				}
			}
		},
		created() {
			this.getProfileInfo();
		},
		methods: {
			async getProfileInfo() {
				const { data: res } = await this.$axios.get(`profile/${this.profile.id}`);
				if (res.status !== 200) return this.$message.error(res.message)
				this.profile = res.data
			},
			// 
			async updateProfile() {
				const { data: res } = await this.$axios.put(`profile/${this.profile.id}`, this.profile)
				if (res.status !== 200) return this.$message.error(res.message)
				this.$message.success(``)
				this.$router.push('/index')
			},
		},
	}
</script>
