<!--  -->
<template>
    <div class="container">
        <div class="loginBox">
            <a-form-model :model="formdata" :rules="rules" ref="loginFormRef" class="loginForm">
                <a-form-model-item prop="username" label="">
                    <a-input v-model="formdata.username" placeholder="">
                        <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
                    </a-input>
                </a-form-model-item>
                <a-form-model-item prop="password" label="">
                    <a-input v-model="formdata.password" placeholder="" type="password" v-on:keyup.enter="login()">
                        <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                    </a-input>
                </a-form-model-item>
                <a-form-model-item class="loginBtn">
                    <a-button type="primary" style="margin: 0 20px;" @click="login()"></a-button>
                    <a-button type="info" style="margin: 0 20px;" @click="reset()"></a-button>
                </a-form-model-item>
            </a-form-model>
        </div>


    </div>
</template>

<script>
    export default {
        data() {
            return {
                formdata: {
                    username: "",
                    password: ""
                },
                rules: {
                    username: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 4, max: 12, message: '412', trigger: 'blur' },
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 6, max: 20, message: '620', trigger: 'blur' },
                    ],
                }
            }
        },
        // - this
        created() {

        },
        // - DOM
        mounted() {

        },
        //
        methods: {
            // 
            reset() {
                this.$refs.loginFormRef.resetFields();
            },
            // 
            login() {
                this.$refs.loginFormRef.validate(async val => {
                    if (!val) {
                        this.$message.error("");
                        return;
                    }
                    const { data: res } = await this.$axios.post("login", this.formdata);
                    // console.log(res);
                    if (res.status != 200) {
                        this.$message.error(res.message);
                        return;
                    }
                    window.sessionStorage.setItem("token", res.token);
                    this.$router.push("/index");

                });
            }

        }
    }
</script>

